.colorPallete {
  max-width: 600px;
  display: flex;

}

.color-item {
  display: block;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
  margin: 0 4px;
  padding: 2px;
}

.dynamic-color-item-space {
  display: flex;
  flex-direction: row-reverse;
}

.custom-color-item {
  display: block;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin: 0 4px;
  padding: 2px;
}

i {
  color: gray;
  margin-right: -1;
}

.customColorPallete {
  display: flex;
  max-width: 600px;

}
form button {
  margin-left: 10px;
  background: skyblue;
  padding: 4px 6px;
  color: black;
  border-radius: 6px;
  outline: none;
  border: none;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.header {
  height: 80px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

#plus-icon:hover {
  cursor: pointer;
}

#plus-icon {
  margin-right: 10px;
  margin-left: 16px;
}

.drawer {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

h2 {
  width: 200px;
  margin: 0 auto;  
}
div.wrapper {
  display: flex;
  height: 96vh;
  width: 100%;
  flex-direction: column;
}

